import React, { Suspense, useState, lazy } from 'react';
import Modal from 'react-modal';
import store from '../Store';
import { hot } from 'react-hot-loader/root';
import timeLine from '../time-line-info.json';

const LazyLightBox = lazy(() => import('react-image-lightbox'));

const SliderModal = (props) => {
    const [activeIndex, setActiveIndex] = useState(0)
    const onModalClose = () => {
        props.onClose();
    }
    const images = timeLine[store.currentTime].slides;
    return (
        <div data-testid="desktop-slider">
            <Suspense fallback={<div>Loading...</div>}>
                <LazyLightBox
                    mainSrc={images[activeIndex]}
                    nextSrc={images[(activeIndex + 1) % images.length]}
                    prevSrc={images[(activeIndex + images.length - 1) % images.length]}
                    onCloseRequest={onModalClose}
                    onMovePrevRequest={() =>
                        setActiveIndex((activeIndex + images.length - 1) % images.length)
                    }
                    onMoveNextRequest={() =>
                        setActiveIndex((activeIndex + 1) % images.length)
                    }
                />
            </Suspense>

        </div>
    );
}

export default hot(SliderModal);
