import React, { useEffect } from 'react';
import { DesktopStyledTimeLine, MobileStyledTimeLine, ExperienceInner, MobileExperienceInner } from '../../Styled';
import DesktopLaptopSlider from './LaptopSlider/desktop';
import MobileLaptopSlider from './LaptopSlider/mobile';
import SliderModal from '../../SliderModal'
import store from '../../Store';
import { observer } from 'mobx-react';
import SEO from '../../components/seo';
import { autorun, toJS } from 'mobx';
const Experience = () => {
  useEffect(() => {
    store.reset();
  }, [])

  return (
    <>
      <SEO title={'ArminYahya - Experience'} description="Armin Yahya Programming Experience" />
      <ExperienceInner>
        <DesktopStyledTimeLine />
        <DesktopLaptopSlider />
        {store.openDesktopSliderModal && <SliderModal onClose={() => { store.openDesktopSliderModal = false }} />}
      </ExperienceInner>
      <MobileExperienceInner>
        <MobileStyledTimeLine />
        <MobileLaptopSlider />
        {store.openMobileSliderModal && <SliderModal onClose={() => { store.openMobileSliderModal = false }} />}
      </MobileExperienceInner>
    </>
  );
}

export default observer(Experience);
